import auth from './tools/storage.js';
import logButton from './buttons/log.js';
import mainWindow from './login/main_login_window.js';
import logMarkup from './markup/log.js';
import profileMarkup from './markup/online-profile.js';

const exit = document.querySelector('#profile-exit');
const profile = document.querySelector('#list-profile-list');

// слушаем кнопку "Выход"
exit.addEventListener('click', function () {
  document.querySelector('#login').classList.remove('d-none');
  document.querySelector('#app').classList.add('d-none');
  logMarkup.setUnregistered();
  logButton.stopListLogHandler();
  auth.clean();
});

if (auth.isSetFlag) {
  document.querySelector('#app').classList.remove('d-none');

} else {
  // ==========АВТОРИЗАЦИЯ==========
  document.querySelector('#login').classList.remove('d-none');
  mainWindow.firstScreen();

}

// ==========ЖУРНАЛ==========
logButton.init();
logButton.startListLogHandler();
console.log(auth.data);

// ==========ОНЛАЙН/ПРОФИЛЬ==========
profile.addEventListener('click', profileMarkup.setProfile);
// profileMarkup.setProfile();




