import auth from '../tools/storage.js';

const listProfile = document.querySelector('#list-profile');

const prepareProfileMarkup = () => `
  <div id="profile" class="card" style="width: 20rem;">
    <div class="card-header">
      Личный кабинет
    </div>
    <ul class="list-group list-group-flush">
      <li id="profile-name" class="list-group-item">${auth.data.nickname}</li>
      <li id="profile-time" class="list-group-item">${auth.data.lastLogin}</li>
      <li id="profile-directory" class="list-group-item">${auth.data.directory}</li>
      <li id="profile-email" class="list-group-item">${auth.data.email}</li>
    </ul>
  </div>`;

export default {
  setProfile() {
    console.log(auth.isSetFlag);
    if (auth.isSetFlag) {
      console.log(prepareProfileMarkup());
      console.log(listProfile);
      listProfile.innerHTML = prepareProfileMarkup();
    } else {
      listProfile.innerHTML = '<p id="unregistered-profile">ПРОФИЛЬ (Пожалуйста, зарегистрируйтесь...)</p>';
    }
  }
};



