const listLogBody = document.querySelector('#log-body');

export default {

  cleanContainer() {
    listLogBody.innerHTML = '';
  },

  setUnregistered() {
    listLogBody.innerHTML = 'Пожалуйста, зарегистрируйтесь...';
  },

  getLogTableRowMarkup(rowElements) {
    return (rowElements[1]) ? `<li class="list-group-item"><b>${rowElements[0]}: </b>${rowElements[1]}</li>` : null;
  },

  getElement(item, index) {
    const getIconColor = (item.ha_operator_hex) ? item.ha_operator_hex : 'F4002C';
    let hasMinusInComments = item.ha_comment.includes('-');
    let imgName = '';

    if (item.ha_kontr_agent_id_fk) {
      imgName = 'buyers';
    }
    if (item.ha_nomenclature_card_id_fk) {
      imgName = 'ic_my_nomenclature';
    }
    if (item.ha_group_good_id_fk) {
      imgName = 'groups';
    }
    if (item.ha_good_id_fk || item.ha_price_id_fk) {
      imgName = 'goods';
    }
    if (item.ha_tag_id_fk) {
      imgName = 'ic_my_tag';
    }

    if (item.ha_balance_act_id_fk && hasMinusInComments) {
      imgName = 'expenses';
    } else if (item.ha_balance_act_id_fk && !hasMinusInComments) {
      imgName = 'revenue';
    }

    if (item.ha_naklad_id_fk && hasMinusInComments) {
      imgName = 'admission';
    } else if (item.ha_naklad_id_fk && !hasMinusInComments) {
      imgName = 'sale';
    }

    imgName = (imgName) ? imgName : 'other_ic_history';

    let cardHeader = item.ha_comment.split('\n');
    cardHeader[1] = (cardHeader[1]) ? cardHeader[1] : '';

    return `
    <div class="card mb-2 p-1" style="width: 100%">
      <div class="media">
        <img class="mr-3 rounded-circle p-1" src="img/user-male-filled-32.png" title="${item.ha_operator_name}" style="background-color: #${getIconColor}" width="50" alt="${item.ha_operator_name}">
        <img class="mr-3" src="img/${imgName}.png" width="50" alt="Generic placeholder image">
        <div class="media-body">
          <h6 class="mt-0">${cardHeader[0]}</h5>
          ${cardHeader[1]}
          <span class="badge text-right text-muted w-100">${new Date(+(item.ha_time + '000')).toLocaleString()} *${index} *${item.ha_id}</span>
        </div>
      </div>
    <!--
    <div id="exampleAccordion" data-children=".item">
      <div class="item">
        <a data-toggle="collapse" data-parent="#exampleAccordion" href="#exampleAccordion${item.ha_id}" role="button" aria-expanded="false" aria-controls="exampleAccordion1">
          <p class="text-right">Таблица со значениями переменных</p>
        </a>
        <div id="exampleAccordion${item.ha_id}" class="collapse" role="tabpanel">
          <p class="mb-3">
            <div class="card m-2" style="width: 100%;"><ul class="list-group list-group-flush">${Object.entries(item).map(this.getLogTableRowMarkup).join('')}</ul></div>
          </p>
        </div>
      </div>
    </div>
    -->`;
  },

  addCardToContainer(cardMarkupItem) {
    listLogBody.insertAdjacentHTML('beforeend', cardMarkupItem);
  },

};



